import authStore from "../shared/store/authStore"
import Duo from "../widgets/scenario/Duo/Duo"
import One from "../widgets/scenario/One/One"
import gifts from '../shared/lib/gift.config.json'

const Page = () => {
    const couple = authStore(store => store.couple)
    const url = authStore(store => store.url)

    const condidate = gifts.people[url]

    return couple ? <Duo {...condidate}/> : <One {...condidate}/>
}

export default Page