import style from './Field.module.scss'

const Field = ({ label, inputProps }) => {
    return (
        <div className={style.field}>
            { label && <p>{ label }</p> }
            <div className={style.input__container}>

                <input {...inputProps} className={style.input} spellCheck={false} />
            </div>
        </div>
    )
}

export default Field