
import { create } from "zustand";

const pairKeys = {
    'kIM9gf4cdfRW84N': 'Aa78qCgZMbz1W0U',    // parent
    'Aa78qCgZMbz1W0U': 'kIM9gf4cdfRW84N',    // parent

    'hSQ6mlgOXo': '9hqIW0fU8n',             // grandmother & grandfather
    '9hqIW0fU8n': 'hSQ6mlgOXo',             // grandmother & grandfather

    'EleMtIhxRzVc56A': 'MnVt0VT4oslKX3o',   // pavel & eugenia
    'MnVt0VT4oslKX3o': 'EleMtIhxRzVc56A',   // pavel & eugenia

    'XPt08I8wDf': 'D8qLP9LIcf',             // lyudmila & vasily
    'D8qLP9LIcf': 'XPt08I8wDf',             // lyudmila & vasily
}

const keysRouter = {
    'iRDPc0Wfe1bn0RD': 'mother',

    'kIM9gf4cdfRW84N': 'svetlana',
    'Aa78qCgZMbz1W0U': 'alexander',

    'hSQ6mlgOXo': 'grandmother',
    '9hqIW0fU8n': 'grandfather',

    'EleMtIhxRzVc56A': 'pavel',
    'MnVt0VT4oslKX3o': 'eugenia',

    'Rs85aSSR5EY6MXA': 'daria',

    'A2g8LrHG6DX05pK': 'anastasia',

    'TnTUhVa5lJ3tHzJ': 'peter',

    'XPt08I8wDf': 'lyudmila',
    'D8qLP9LIcf': 'vasily'
}

const pathsOne = [ 'mother', 'daria', 'anastasia', 'peter' ]


const authStore = create(
    (set, get) => ({
        couple: false,
        url: '',
        pairKey: '',
        login: (key) => {
            const url = keysRouter[key]

            if (!url) {
                set({url: ''})
                return ''
            }

            if (pathsOne.find(path => path === url)) set({couple: false})
            else set({couple: true})

            const pairKey = pairKeys[key] || ''

            set({url: url, pairKey: pairKey})

            return url
        },
        isPair: (key) => {
            return get().pairKey === key
        }
}))

export default authStore