import { useEffect, useState } from "react"
import { VscArrowSmallRight, VscArrowSmallLeft } from "react-icons/vsc";
import style from './One.module.scss'
import Button from "../../../shared/Button/Button";
import { poopetti } from 'poopetti'


const One = ({ gift, attention}) => {
    const [isCheck, setIsCheck] = useState(false)
    const [index, setIndex] = useState(0)

    useEffect(() => {
        if (isCheck) {
            poopetti({
                emoji: '',
                duration: 1700,
                density: 236,
                radius: 200,
                content: ''
            })
        }
    }, [isCheck])

    const handelNext = () => {
        const newIndex = index + 1
        if (newIndex < attention.length)
            setIndex(newIndex)
        else setIsCheck(true)
    }
    const handelLast = () => {
        const lastIndex = index - 1
        if (lastIndex > -1)
            setIndex(lastIndex)
    }
    return (
        <div className={style.one}>
            <div className={style.one__area}>
                <p>
                    { !isCheck ? attention[index] : gift}
                </p>
                <div className={`${style.one__atom} ${style.one__atom_top} ${!isCheck ? style.one__atom_transparent : ''}`}></div>
                <div className={`${style.one__atom} ${style.one__atom_right} ${!isCheck ? style.one__atom_transparent : ''}`}></div>
                <div className={`${style.one__atom} ${style.one__atom_bottom} ${!isCheck ? style.one__atom_transparent : ''}`}></div>
                <div className={`${style.one__atom} ${style.one__atom_left} ${!isCheck ? style.one__atom_transparent : ''}`}></div>
            </div>
            <div className={style.one__btns}>
                {
                    !isCheck && 
                    <Button props={{onClick: handelLast, disabled: !index}}>
                        <VscArrowSmallLeft size={30}/>
                    </Button>
                }
                {
                    !isCheck && 
                    <Button props={{onClick: handelNext}}>
                        <VscArrowSmallRight size={30}/>
                    </Button>
                }
            </div>
            
        </div>
    )
}

export default One