import { useEffect, useState } from "react"
import { VscArrowSmallRight, VscArrowSmallLeft } from "react-icons/vsc";
import style from './Duo.module.scss'
import Button from "../../../shared/Button/Button";
import Field from "../../../shared/Field/Field";
import authStore from "../../../shared/store/authStore";
import { poopetti } from 'poopetti'

const Duo = ({ gift, attention, secondAttention}) => {
    const [isCheck, setIsCheck] = useState(false)
    const [index, setIndex] = useState(0)
    const [isCheckSecond, setIsCheckSecond] = useState(false)

    const [value, setValue] = useState('')
    const [showGift, setShowGift] = useState(false)

    const isPair = authStore(store => store.isPair)

    useEffect(() => {
        if (showGift) {
            poopetti({
                emoji: '',
                duration: 1700,
                density: 236,
                radius: 200,
                content: ''
            })
        }
    }, [showGift])

    const handelNext = () => {
        const newIndex = index + 1
        if (newIndex < attention.length)
            setIndex(newIndex)
        else setIsCheck(true)
    }
    const handelLast = () => {
        const lastIndex = index - 1
        if (lastIndex > -1)
            setIndex(lastIndex)
    }
    const onChange = (event) => {
        const secondKey = event.target.value
        setValue(secondKey)

        if (isPair(secondKey)) {
            setShowGift(true)
            setIsCheckSecond(true)
        }
        else {
            setShowGift(false)
            setIsCheckSecond(false)
        }
    }
    return (
        <div className={style.duo}>
            <div className={style.duo__area}>
                <p>
                    { 
                        !isCheck ? attention[index] : 
                            !showGift ? <Field label='' inputProps={{placeholder: ' ', value: value, onChange: onChange}}/> :
                                gift
                                
                    }
                </p>
                <div className={`${style.duo__atom} ${style.duo__atom_top} ${!showGift ? style.duo__atom_transparent : ''}`}></div>
                <div className={`${style.duo__atom} ${style.duo__atom_right} ${!showGift ? style.duo__atom_transparent : ''}`}></div>
                <div className={`${style.duo__atom} ${style.duo__atom_bottom} ${!showGift ? style.duo__atom_transparent : ''}`}></div>
                <div className={`${style.duo__atom} ${style.duo__atom_left} ${!showGift ? style.duo__atom_transparent : ''}`}></div>
            </div>
            <div className={style.duo__btns}>
                {
                    !isCheck && !showGift &&
                    <Button props={{onClick: handelLast, disabled: !index}}>
                        <VscArrowSmallLeft size={30}/>
                    </Button>
                }
                {
                    !isCheck && !showGift &&
                    <Button props={{onClick: handelNext}}>
                        <VscArrowSmallRight size={30}/>
                    </Button>
                }
            </div>
            
        </div>
    )
}

export default Duo